import { useState, useEffect } from "react";

export const useDictionary = (dictionaryName, updater, page) => {
    const [dictionary, setDictionary] = useState(null);

    const getUrl = (page) =>
        page
            ? "/dictionaries-service/" + dictionaryName + "/" + page
            : "/dictionaries-service/" + dictionaryName;

    useEffect(() => {
        setDictionary(null);
        fetch(getUrl(page), {
            method: "GET",
        })
            .then((response) => response.json())
            .then(setDictionary);
    }, [dictionaryName, updater, page]);

    return dictionary;
};
