/**
 * ajaxCall
 *
 * @param {string} method
 * @param {object} body
 * @param {string} csrf
 * @returns {true!false}
 */
export const ajaxCall = async ($url, method, body, csrf) => {
    showLoader();

    const response = await fetch($url, {
        headers: {
            "Content-Type": "application/json",
            Accept: "application/json, text-plain, */*",
            "X-Requested-With": "XMLHttpRequest",
            "X-CSRF-TOKEN": csrf,
        },
        method: method,
        credentials: "same-origin",
        body: JSON.stringify(body),
    });

    hideLoader();

    return response.ok;
};
