/**
 * showLoader
 *
 * Show the system loader
 */
showLoader = () => {
    document.getElementById("loader-container").style.display = "block";
};

/**
 * hideLoader
 *
 * Hide the system loader
 */
hideLoader = () => {
    document.getElementById("loader-container").style.display = "none";
};

/**
 * showAlert
 *
 * Show alert message
 *
 * @param {string} type
 * @param {string} message
 */
showAlert = (type, message) => {
    // Delete old message
    if (document.getElementById("alert-message")) {
        document.getElementById("alert-message").remove();
    }
    // Button close
    const button = document.createElement("button");
    button.setAttribute("type", "button");
    button.setAttribute("class", "btn-close");
    button.setAttribute("data-bs-dismiss", "alert");
    button.setAttribute("arai-label", "Close");
    // Div
    const div = document.createElement("div");
    div.setAttribute("id", "alert-message");
    div.setAttribute(
        "class",
        `alert alert-dismissible alert-${type} fade show`
    );
    div.setAttribute("role", "alert");
    div.innerHTML = message;
    div.append(button);
    // Append alert in page
    document.getElementById("alert-container").append(div);
};
