import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Modal, Button, Form, Row, Col } from "react-bootstrap";
import { ajaxCall } from "../../lib/lib";

function DictionaryModal({
    mode,
    onHide,
    currentId,
    currentValue,
    saveLabel,
    cancelLabel,
    deleteLabel,
    dictionary,
    modalDeleteTitle,
    modalEditTitle,
    modalAddTitle,
    modalDeleteConfirm,
    csrf,
}) {
    const [value, setValue] = useState("");
    const [title, setTitle] = useState("");

    useEffect(() => {
        setValue(currentValue);
    }, [currentValue]);

    useEffect(() => {
        switch (mode) {
            case "edit":
                setTitle(modalEditTitle);
                break;
            case "add":
                setTitle(modalAddTitle);
                break;
            case "delete":
                setTitle(modalDeleteTitle);
                break;
        }
    }, [mode]);

    const handleSubmitClick = async (e) => {
        switch (mode) {
            case "edit":
                onHide(
                    true,
                    await ajaxCall(
                        "/dictionaries-service",
                        "put",
                        {
                            id: currentId,
                            value: value,
                            dictionary: dictionary,
                        },
                        csrf
                    )
                );
                break;
            case "add":
                onHide(
                    true,
                    await ajaxCall(
                        "/dictionaries-service",
                        "post",
                        {
                            value: value,
                            dictionary: dictionary,
                        },
                        csrf
                    )
                );
                break;
            case "delete":
                onHide(
                    true,
                    await ajaxCall(
                        "/dictionaries-service",
                        "delete",
                        {
                            id: currentId,
                            dictionary: dictionary,
                        },
                        csrf
                    )
                );
                break;
        }
    };

    const handleCloseModal = () => {
        onHide(false, false);
    };

    const handleChange = (e) => {
        setValue(e.target.value);
    };

    return (
        <Modal
            show={mode !== ""}
            onHide={() => {
                onHide(false, false);
            }}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    {title}{" "}
                    {mode === "delete" ? (
                        <span className="fs-4 badge bg-dark">
                            {currentValue}
                        </span>
                    ) : (
                        ""
                    )}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Row>
                        <Col>
                            {mode !== "delete" ? (
                                <Form.Group
                                    className="mb-3"
                                    controlId={currentId}
                                >
                                    <Form.Control
                                        type="text"
                                        value={value}
                                        onChange={handleChange}
                                    />
                                </Form.Group>
                            ) : (
                                modalDeleteConfirm
                            )}
                        </Col>
                    </Row>
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleCloseModal}>
                    {cancelLabel}
                </Button>
                {mode !== "delete" ? (
                    <Button variant="primary" onClick={handleSubmitClick}>
                        {saveLabel}
                    </Button>
                ) : (
                    <Button variant="danger" onClick={handleSubmitClick}>
                        {deleteLabel}
                    </Button>
                )}
            </Modal.Footer>
        </Modal>
    );
}

DictionaryModal.propTypes = {
    mode: PropTypes.string.isRequired,
    onHide: PropTypes.func.isRequired,
    currentId: PropTypes.string.isRequired,
    currentValue: PropTypes.string.isRequired,
    saveLabel: PropTypes.string.isRequired,
    cancelLabel: PropTypes.string.isRequired,
    deleteLabel: PropTypes.string.isRequired,
    dictionary: PropTypes.string.isRequired,
    modalDeleteTitle: PropTypes.string.isRequired,
    modalEditTitle: PropTypes.string.isRequired,
    modalAddTitle: PropTypes.string.isRequired,
    modalDeleteConfirm: PropTypes.string.isRequired,
    csrf: PropTypes.string.isRequired,
};

export default DictionaryModal;
