import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {
    Container,
    Modal,
    Button,
    Row,
    Col,
    Form,
    InputGroup,
    FormControl,
} from "react-bootstrap";

function AgentModal({
    mode,
    onHide,
    saveLabel,
    deleteLabel,
    cancelLabel,
    agent,
    csrf,
    modalDeleteTitle,
    modalEditTitle,
    modalAddTitle,
    modalDeleteConfirm,
    firstname,
    lastname,
    description,
    agenttype,
    startdate,
    enddate,
    agentTypeData,
}) {
    const [title, setTitle] = useState("");

    const parseData = (key, data) => {
        switch (key) {
            case "startdate":
            case "enddate":
                return data && data[key] ? new Date(data[key]) : "";
            default:
                return data && data[key] ? data[key] : "";
        }
    };

    useEffect(() => {
        switch (mode) {
            case "edit":
                setTitle(modalEditTitle);
                break;
            case "add":
                setTitle(modalAddTitle);
                break;
            case "delete":
                setTitle(modalDeleteTitle);
                break;
        }
    }, [mode]);

    const handleCloseModal = () => {
        onHide(false, false);
    };

    const onInputChange = (e) => {
        console.log(e);
    };

    return agent ? (
        <Modal
            show={mode !== ""}
            onHide={() => {
                onHide(false, false);
            }}
            size="xl"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    {title}{" "}
                    {mode === "delete" ? (
                        <span className="fs-4 badge bg-dark">
                            {currentValue}
                        </span>
                    ) : (
                        ""
                    )}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className="show-grid">
                <Container>
                    <Row>
                        <Col xs={12} md={6}>
                            <Form.Label htmlFor="firstname">
                                {firstname}
                            </Form.Label>
                            <InputGroup className="mb-3">
                                <FormControl
                                    id="firstname"
                                    value={parseData("firstname", agent)}
                                    onChange={onInputChange}
                                />
                            </InputGroup>
                        </Col>
                        <Col xs={12} md={6}>
                            <Form.Label htmlFor="lastname">
                                {lastname}
                            </Form.Label>
                            <InputGroup className="mb-3">
                                <FormControl
                                    id="lastname"
                                    value={parseData("lastname", agent)}
                                    onChange={onInputChange}
                                />
                            </InputGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={12} md={4}>
                            <Form.Label htmlFor="agenttype">
                                {agenttype}
                            </Form.Label>
                            <InputGroup className="mb-3">
                                <Form.Select
                                    id="agenttype"
                                    onChange={onInputChange}
                                    value={agent["agenttype_id"]}
                                >
                                    <option>-</option>
                                    {agentTypeData
                                        ? agentTypeData.dictionary.values.map(
                                              (value, index) => (
                                                  <option
                                                      key={index}
                                                      value={value.id}
                                                  >
                                                      {value.value}
                                                  </option>
                                              )
                                          )
                                        : ""}
                                </Form.Select>
                            </InputGroup>
                        </Col>
                        <Col xs={12} md={4}>
                            <Form.Label htmlFor="firstname">
                                {startdate}
                            </Form.Label>
                            <InputGroup className="mb-3">
                                <DatePicker
                                    id="startdate"
                                    onChange={onInputChange}
                                    className="p-2 border rounded w-100"
                                    selected={parseData("startdate", agent)}
                                />
                            </InputGroup>
                        </Col>
                        <Col xs={12} md={4}>
                            <Form.Label htmlFor="enddate">{enddate}</Form.Label>
                            <InputGroup className="mb-3">
                                <DatePicker
                                    id="enddate"
                                    onChange={onInputChange}
                                    className="p-2 border rounded w-100"
                                    selected={parseData("enddate", agent)}
                                />
                            </InputGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <Form.Label htmlFor="description">
                                {description}
                            </Form.Label>
                            <InputGroup className="mb-3">
                                <Form.Control
                                    as="textarea"
                                    id="description"
                                    style={{ height: "100px" }}
                                    onChange={onInputChange}
                                />
                            </InputGroup>
                        </Col>
                    </Row>
                </Container>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleCloseModal}>
                    {cancelLabel}
                </Button>
                {mode !== "delete" ? (
                    <Button variant="primary">{saveLabel}</Button>
                ) : (
                    <Button variant="danger">{deleteLabel}</Button>
                )}
            </Modal.Footer>
        </Modal>
    ) : (
        ""
    );
}

AgentModal.propTypes = {
    mode: PropTypes.string.isRequired,
    onHide: PropTypes.func.isRequired,
    saveLabel: PropTypes.string.isRequired,
    deleteLabel: PropTypes.string.isRequired,
    cancelLabel: PropTypes.string.isRequired,
    agent: PropTypes.object,
    csrf: PropTypes.string.isRequired,
    modalDeleteTitle: PropTypes.string.isRequired,
    modalEditTitle: PropTypes.string.isRequired,
    modalAddTitle: PropTypes.string.isRequired,
    modalDeleteConfirm: PropTypes.string.isRequired,
    firstname: PropTypes.string.isRequired,
    lastname: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    agenttype: PropTypes.string.isRequired,
    startdate: PropTypes.string.isRequired,
    enddate: PropTypes.string.isRequired,
    agentTypeData: PropTypes.object,
};

export default AgentModal;
