import { useState, useEffect } from "react";

export const useAgents = (page) => {
    const [agents, setAgents] = useState(null);

    useEffect(() => {
        fetch("/agents-service/" + page, {
            method: "GET",
        })
            .then((response) => response.json())
            .then((data) => {
                setAgents(data);
            });
    }, [page]);

    return agents;
};
