import React from "react";
import PropTypes from "prop-types";
import { Table, Button, Form } from "react-bootstrap";

const ToolTable = ({
    data,
    handleEditClick,
    handleDeleteClick,
    lang,
    deleteLabel,
    editLabel,
    headColor,
    agentTypeData,
}) => {
    const parseData = (key, data) => {
        switch (key) {
            case "agenttype_id":
                return data && agentTypeData
                    ? agentTypeData.dictionary.values[data].value
                    : "-";
            case "startdate":
            case "enddate":
                return data ? new Date(data).toLocaleDateString("it-IT") : "";
            default:
                return data;
        }
    };

    return (
        <Table responsive bordered striped>
            <thead>
                <tr>
                    {Object.keys(data.values[0])
                        .map((columnKey, headIndex) =>
                            columnKey !== "id" ? (
                                <th
                                    style={{ backgroundColor: headColor }}
                                    key={headIndex}
                                >
                                    {lang[columnKey]}
                                </th>
                            ) : null
                        )
                        .filter((item) => item !== null)}
                    <th style={{ backgroundColor: headColor }}></th>
                    <th style={{ backgroundColor: headColor }}></th>
                </tr>
            </thead>
            <tbody>
                {data.values.map((row, rowIndex) => (
                    <tr key={rowIndex}>
                        {Object.keys(row)
                            .map((columnKey, dataIndex) =>
                                columnKey !== "id" ? (
                                    <th key={dataIndex}>
                                        {parseData(columnKey, row[columnKey])}
                                    </th>
                                ) : null
                            )
                            .filter((item) => item !== null)}
                        <th className="text-center">
                            <Button
                                className="col-12"
                                variant="primary"
                                size="sm"
                                onClick={handleEditClick}
                                data-id={rowIndex}
                            >
                                {editLabel}
                            </Button>
                        </th>
                        <th className="text-center">
                            <Button
                                className="col-12"
                                variant="danger"
                                size="sm"
                                onClick={handleDeleteClick}
                            >
                                {deleteLabel}
                            </Button>
                        </th>
                    </tr>
                ))}
            </tbody>
        </Table>
    );
};

ToolTable.propTypes = {
    data: PropTypes.object.isRequired,
    lang: PropTypes.object.isRequired,
    handleEditClick: PropTypes.func.isRequired,
    handleDeleteClick: PropTypes.func.isRequired,
    headColor: PropTypes.string,
    agentTypeData: PropTypes.object,
};

ToolTable.defaultProps = {
    headColor: "#ffffff",
};

export default ToolTable;
