import React, { useState } from "react";
import ReactDOM from "react-dom";
import PropTypes from "prop-types";
import { Container, Button, Row, Col } from "react-bootstrap";
import { useAgents } from "../../hooks/useagents";
import { useDictionary } from "../../hooks/usedictionary";
import AgentModal from "../../components/agent-modal";
import Paginator from "../../components/paginator";
import ToolTable from "../tool-table";

function AgentIndex({
    firstname,
    lastname,
    description,
    agenttype_id,
    startdate,
    enddate,
    editLabel,
    deleteLabel,
    saveLabel,
    cancelLabel,
    addLabel,
    title,
    csrf,
    modalDeleteTitle,
    modalEditTitle,
    modalAddTitle,
    modalDeleteConfirm,
}) {
    const [currentId, setCurrentId] = useState("");
    const [modalMode, setModalMode] = useState("");
    const [message, setMessage] = useState("");
    const [alertShow, setAlertShow] = useState(false);
    const [alertVariant, setAlertVariant] = useState("");
    const [page, setPage] = useState(0);
    const [agent, setAgent] = useState(null);

    const agents = useAgents(page);
    const dictionaryData = useDictionary("agenttypes", null, null);

    const handleEditClick = (e) => {
        handleMessage(false, false);
        setCurrentId(e.target.dataset.id);
        setAgent(agents.values[e.target.dataset.id]);
        setModalMode("edit");
    };

    const handleDeleteClick = (e) => {
        handleMessage(false, false);
        setCurrentId(e.target.dataset.id);
        setAgent(null);
        setModalMode("delete");
    };

    const handleMessage = (visibility, result) => {
        setModalMode("");
        switch (true) {
            case result && visibility:
                setMessage(updateOk);
                setAlertShow(true);
                setAlertVariant("success");
                break;
            case !result && visibility:
                setMessage(updateKo);
                setAlertShow(true);
                setAlertVariant("danger");
                break;
            case !visibility:
                setMessage("");
                setAlertShow(false);
                setAlertVariant("");
                break;
            default:
                setMessage("");
                setAlertShow(false);
                setAlertVariant("");
                break;
        }
    };

    return agents ? (
        <>
            <Container className="mt-4">
                <Row>
                    <Col xs={12} md={10}>
                        <h1>{title}</h1>
                    </Col>
                    <Col className="text-end d-grid gap-2" xs={12} md={2}>
                        <Button variant="secondary" type="button">
                            {addLabel}
                        </Button>
                    </Col>
                </Row>
            </Container>
            <Container className="mt-4">
                <ToolTable
                    data={agents}
                    handleEditClick={handleEditClick}
                    handleDeleteClick={handleDeleteClick}
                    lang={{
                        firstname,
                        lastname,
                        description,
                        agenttype_id,
                        startdate,
                        enddate,
                    }}
                    editLabel={editLabel}
                    deleteLabel={deleteLabel}
                    headColor={agents.headColor}
                    agentTypeData={dictionaryData}
                    agentTypeId={agenttype_id}
                />
            </Container>
            <Container className="mt-4">
                <Paginator
                    setPage={setPage}
                    total={agents ? agents.pages : 1}
                    current={page}
                />
            </Container>
            <AgentModal
                mode={modalMode}
                onHide={handleMessage}
                currentId={currentId}
                saveLabel={saveLabel}
                deleteLabel={deleteLabel}
                cancelLabel={cancelLabel}
                agent={agent}
                csrf={csrf}
                modalDeleteTitle={modalDeleteTitle}
                modalEditTitle={modalEditTitle}
                modalAddTitle={modalAddTitle}
                modalDeleteConfirm={modalDeleteConfirm}
                firstname={firstname}
                lastname={lastname}
                description={description}
                agenttype={agenttype_id}
                startdate={startdate}
                enddate={enddate}
                agentTypeData={dictionaryData}
            />
        </>
    ) : (
        ""
    );
}

AgentIndex.propTypes = {
    editLabel: PropTypes.string.isRequired,
    deleteLabel: PropTypes.string.isRequired,
    saveLabel: PropTypes.string.isRequired,
    cancelLabel: PropTypes.string.isRequired,
    addLabel: PropTypes.string.isRequired,
    firstname: PropTypes.string.isRequired,
    lastname: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    agenttype_id: PropTypes.string.isRequired,
    startdate: PropTypes.string.isRequired,
    enddate: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    csrf: PropTypes.string.isRequired,
    modalDeleteTitle: PropTypes.string.isRequired,
    modalEditTitle: PropTypes.string.isRequired,
    modalAddTitle: PropTypes.string.isRequired,
    modalDeleteConfirm: PropTypes.string.isRequired,
};

export default AgentIndex;

if (document.getElementById("agent-index")) {
    const component = document.getElementById("agent-index");
    ReactDOM.render(<AgentIndex {...component.dataset} />, component);
}
