import React, { useState } from "react";
import ReactDOM from "react-dom";
import PropTypes from "prop-types";
import { Container, Form, Button, Row, Col } from "react-bootstrap";
import DictionaryModal from "../dictionary-modal";
import Paginator from "../paginator";
import Message from "../../components/message";
import { useDictionary } from "../../hooks/usedictionary";

function DictionaryForm({
    dictionary,
    updateOk,
    updateKo,
    title,
    dictionaryName,
    editLabel,
    deleteLabel,
    saveLabel,
    cancelLabel,
    addLabel,
    modalDeleteTitle,
    modalEditTitle,
    modalAddTitle,
    modalDeleteConfirm,
    csrf,
}) {
    const [currentId, setCurrentId] = useState("");
    const [currentValue, setCurrentValue] = useState("");
    const [alertShow, setAlertShow] = useState(false);
    const [message, setMessage] = useState("");
    const [alertVariant, setAlertVariant] = useState("");
    const [modalMode, setModalMode] = useState("");
    const [page, setPage] = useState(1);

    const dictionaryData = useDictionary(dictionary, modalMode, page);

    const handleEditClick = (e) => {
        handleMessage(false, false);
        setCurrentId(e.target.dataset.id);
        setCurrentValue(e.target.dataset.value);
        setModalMode("edit");
    };

    const handleAddClick = (e) => {
        handleMessage(false, false);
        setCurrentId("");
        setCurrentValue("");
        setModalMode("add");
    };

    const handleDeleteClick = (e) => {
        handleMessage(false, false);
        setCurrentId(e.target.dataset.id);
        setCurrentValue(e.target.dataset.value);
        setModalMode("delete");
    };

    const handleMessage = (visibility, result) => {
        setModalMode("");
        switch (true) {
            case result && visibility:
                setMessage(updateOk);
                setAlertShow(true);
                setAlertVariant("success");
                break;
            case !result && visibility:
                setMessage(updateKo);
                setAlertShow(true);
                setAlertVariant("danger");
                break;
            case !visibility:
                setMessage("");
                setAlertShow(false);
                setAlertVariant("");
                break;
            default:
                setMessage("");
                setAlertShow(false);
                setAlertVariant("");
                break;
        }
    };

    return dictionaryData ? (
        <>
            <Message
                show={alertShow}
                variant={alertVariant}
                onClose={() => handleMessage(false, false)}
                message={message}
            />
            <Container className="border rounded shadow mt-5 pt-3 pb-2" fluid>
                <Row xs className="mb-4 pt-4">
                    <Col xs={12} md={10}>
                        <h1>
                            {title}: <strong>{dictionaryName}</strong>
                        </h1>
                    </Col>
                    <Col className="text-end d-grid gap-2" xs={12} md={2}>
                        <Button
                            variant="secondary"
                            type="button"
                            onClick={handleAddClick}
                        >
                            {addLabel}
                        </Button>
                    </Col>
                </Row>
                <Form>
                    {dictionaryData.dictionary.values.map((value, index) => (
                        <Row key={index}>
                            <Col xs={12} md={8}>
                                <Form.Group className="mb-1">
                                    <Form.Control
                                        data-value-id={value.id}
                                        type="text"
                                        defaultValue={value.value}
                                        disabled
                                        id={"dictionary-data-" + value.id}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs={12} md={4}>
                                <Row className="mb-4">
                                    <Col className="d-grid gap-2">
                                        <Button
                                            variant="primary"
                                            type="button"
                                            onClick={handleEditClick}
                                            data-id={value.id}
                                            data-value={value.value}
                                        >
                                            {editLabel}
                                        </Button>
                                    </Col>
                                    <Col className="d-grid gap-2">
                                        <Button
                                            variant="danger"
                                            type="button"
                                            onClick={handleDeleteClick}
                                            data-id={value.id}
                                            data-value={value.value}
                                        >
                                            {deleteLabel}
                                        </Button>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    ))}
                </Form>
            </Container>
            <Container className="mt-4">
                <Paginator
                    setPage={setPage}
                    total={
                        dictionaryData && dictionaryData.dictionary
                            ? dictionaryData.dictionary.pages
                            : 1
                    }
                    current={page}
                />
            </Container>
            <DictionaryModal
                mode={modalMode}
                onHide={handleMessage}
                currentId={currentId}
                currentValue={currentValue}
                saveLabel={saveLabel}
                deleteLabel={deleteLabel}
                cancelLabel={cancelLabel}
                dictionary={dictionary}
                modalDeleteTitle={modalDeleteTitle}
                modalEditTitle={modalEditTitle}
                modalAddTitle={modalAddTitle}
                modalDeleteConfirm={modalDeleteConfirm}
                csrf={csrf}
            />
        </>
    ) : (
        ""
    );
}

DictionaryForm.propTypes = {
    dictionary: PropTypes.string.isRequired,
    updateOk: PropTypes.string.isRequired,
    updateKo: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    dictionaryName: PropTypes.string.isRequired,
    editLabel: PropTypes.string.isRequired,
    deleteLabel: PropTypes.string.isRequired,
    saveLabel: PropTypes.string.isRequired,
    cancelLabel: PropTypes.string.isRequired,
    addLabel: PropTypes.string.isRequired,
    csrf: PropTypes.string.isRequired,
    modalDeleteTitle: PropTypes.string.isRequired,
    modalEditTitle: PropTypes.string.isRequired,
    modalAddTitle: PropTypes.string.isRequired,
    modalDeleteConfirm: PropTypes.string.isRequired,
};

export default DictionaryForm;

if (document.getElementById("dictionary-form")) {
    const component = document.getElementById("dictionary-form");
    ReactDOM.render(<DictionaryForm {...component.dataset} />, component);
}
